const template = document.createElement("template");
template.innerHTML = `
    <div>
        <span>
            <button class="popUPbutton-accept"></button>
            <button class="popUPbutton-cancel"></button>
        </span>
    </div>
`;

class CustomPopUp extends HTMLElement {
  constructor() {
    super();
    let templateContent = template.content.cloneNode(true);
    this.attachShadow({ mode: "open" }).append(templateContent);
  }
}

customElements.define("my-custom-popup", CustomPopUp);
